*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

@font-face {
  font-display: swap;
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
}

.container {
  margin-right: auto;
  margin-left: auto;
}
@media (max-width: 767px) {
  .container {
    width: 280px;
  }
}
@media (min-width: 768px) {
  .container {
    width: 708px;
  }
}
@media (min-width: 1320px) {
  .container {
    width: 1220px;
  }
}

.button {
  display: inline-block;
  color: #ffffff;
  background-color: #68b738;
  border: 0;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
}

.button--secondary {
  color: #444444;
  background-color: #f2f2f2;
}

.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.logo__name {
  margin: 0 auto;
}

@media (min-width: 768px) {
  .page-header__logo.logo {
