  }
}

.page {
  min-width: 320px;
  height: 100%;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
}

.page__body {
  margin: 0;
}

@media (min-width: 768px) {
  .page-header__nav {
    display: grid;
    grid-template-columns: 174px 1fr;
    column-gap: 20px;
    width: 708px;
    margin: 0 auto;
    padding: 24px 0;
  }
}
@media (min-width: 1320px) {
  .page-header__nav {
    width: 1220px;
    padding: 53px 0 29px;
  }
}

@media (max-width: 767px) {
  .page-header__bar {
    display: grid;
    width: 320px;
    margin: 0 auto;
    grid-template-columns: 34px 1fr 34px;
    padding: 12px 20px 16px;
    align-items: center;
  }
}

.page-header__menu {
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .page-header__menu {
    margin-right: -17px;
  }
}

@media (max-width: 767px) {
  .page-header__logo {
    grid-column: 1/span 2;
  }
}
@media (min-width: 768px) {
  .page-header__logo .logo__name {
    display: none;
  }
}

.page-header__toggler {
  grid-column: -2;
  justify-self: right;
  position: relative;
  width: 24px;
  height: 24px;
  padding: 0;
  border: none;
}
@media (min-width: 768px) {
  .page-header__toggler {
    display: none;
  }
}

.page-header__toggler--close::before, .page-header__toggler--close::after {
  position: absolute;
  top: 0;
  left: 11px;
  width: 1.5px;
  height: 25px;
  content: " ";
  background-color: #000000;
}
.page-header__toggler--close::before {
  transform: rotate(45deg);
}
.page-header__toggler--close::after {
  transform: rotate(-45deg);
}

